/*snackbar*/
#snackbar {
  /* visibility: hidden; */
  min-width: 300px;
  max-width: 300px;
  margin-left: -150px;
  background-color: #005EA5;
  color: #ffffff;
  text-align: center;
  border-radius: 2px;
  padding: 16px;
  position: fixed;
  z-index: 1001;
  left: 50%;
  bottom: 30px;
  font-size: 17px;
}

#snackbar[error="true"]{
  background-color: #B10E1E;
}

.error-color{
  color: #B10E1E;
}

.correct-color{
  color: #00823B;
}

.warning-cell {
  color:#DF3034;  
  font-weight: bold;
}

#snackbar[warning="true"]{
  background-color: #FFBF47;
}

#snackbar.show {
  visibility: visible;
  -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
  animation: fadein 0.5s, fadeout 0.5s 2.5s;
}

@-webkit-keyframes fadein {
  from {bottom: 0; opacity: 0;}
  to {bottom: 30px; opacity: 1;}
}

@keyframes fadein {
  from {bottom: 0; opacity: 0;}
  to {bottom: 30px; opacity: 1;}
}

@-webkit-keyframes fadeout {
  from {bottom: 30px; opacity: 1;}
  to {bottom: 0; opacity: 0;}
}

@keyframes fadeout {
  from {bottom: 30px; opacity: 1;}
  to {bottom: 0; opacity: 0;}
}

